* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
header {
  background-color: lightskyblue;
  padding: 1rem 0;
  color: white;
  text-align: center;
  font-size: 2rem;
}
.list {
  max-width: 500px;
  margin: auto;
}
.box {
  padding: 1.5rem;
  border: 2px solid skyblue;
  border-radius: 0.5rem;
  margin: 1rem 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.title {
  font-size: 1.25rem;
  font-weight: bold;
  color: blue;
}
.date {
  color: #333;
}
.like_box {
  border: 1px solid lightblue;
  padding: 1rem;
  border-radius: 0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.like_btn {
  cursor: pointer;
}

/* Modal */
.modal {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal .con {
  background-color: white;
  padding: 1rem;
  border-radius: 1rem;
  min-height: 300px;
  width: 50%;
  max-width: 500px;
  position: relative;
}

.modal h1 {
  text-align: center;
}

.modal p {
  padding: 1rem 0;
}

.close {
  position: absolute;
  top: 0;
  right: -50px;
}
